import React, { useReducer, useEffect } from 'react'
import PersonForm from './components/PersonForm'
import PhoneBook from './components/PhoneBook'
import Filter from './components/Filter'
import PersonField from './components/PersonField'
import Notification from './components/Notification'
import personService from './services/persons'

const App = () => {

  const [ state, updateState ] = useReducer(
    (state, newState) => ({...state, ...newState}),
    {name: '', number: '', filter: '', persons: [], filteredPersons: [], notification: '', notificationType: '',
    }
  )

  const { name, number, filter, persons, filteredPersons, notification, notificationType } = state

  useEffect(() => {
    console.log('effect')
    personService.getAll()
    .then(response => {
      console.log('promise fulfilled')
      updateState( { persons: response.data } )
    })
  }, [])
  
  const addPerson = (event) => {
    event.preventDefault()
    const newObject = {
      name: name,
      phone: number,
    }
    persons.filter(person=>name.toLowerCase()===person.name.toLowerCase()).length > 0
    ? updatePerson(name, newObject)
    : personService
    .create(newObject)
    .then(response=>{
      console.log(response.data)
      updateState({ persons: persons.concat(response.data), 
        notification: `${name} has been succesfully added`, 
        notificationType: 'success' })
      console.log('notf type: ', notificationType)
      setTimeout(() => {
        updateState({ notification: '', notificationType: ''})
      }, 5000);
    })
    updateState({ name: '', number: '' })
  }

  const updatePerson = (name, newObject) => {
    if (window.confirm(`${name} already exists, update?`)){
      const personId = persons.find(person=>name.toLowerCase()===person.name.toLowerCase()).id
      newObject = {...newObject,
                    id: personId}
      personService.update(personId, newObject)
        .then(response=>{
        console.log(response.data)
        updateState({ 
          persons: persons.map(person => name.toLowerCase()===person.name.toLowerCase() ? 
          newObject : 
          person) })
      })
      .catch(error=>console.log('error react: ',error))
    }
  }

  const deletePerson = (id) => {
      personService.deletePerson(id)
      .then(()=>{
        updateState({ persons: persons.filter(person=>person.id!==id)})
        
      })
      .catch(error => {
        console.log('ENTERED CATCH')
        updateState({  
          notification: `${persons.find(person => person.id === id).name} is already deleted`, 
          notificationType: 'error', 
          persons: persons.filter(person=>person.id!==id)})
          setTimeout(() => {
            updateState({ notification: '', notificationType: ''})
          }, 5000);
      })
      console.log('deleted person ', id)
      
      
  }

  const handleFilterChange = e => {
    updateState({
      filter: e.target.value, 
      filteredPersons: persons.filter(person=>
        person.name.toLowerCase().includes(filter.toLowerCase())
        )
      }
    )
  }


  console.log(`filtered persons: ${filteredPersons}, filter: ${filter}`);
  return (
    <div>
      <h2>Phonebook</h2>
      <Notification type={notificationType} message={notification}/>
      <Filter
        handleFilterChange={handleFilterChange}
        newFilter={filter}/>
      <h2>Add new</h2>
      <PersonForm addPerson={addPerson}>
        <PersonField name='name' value={name} onChange={e => updateState({ name: e.target.value })}/>
        <PersonField name='phone' value={number} onChange={e => updateState({ number: e.target.value })}/>
      </PersonForm>
        
      <h2>Numbers</h2>
      <PhoneBook persons={filter==='' ? persons : filteredPersons} deletePerson={deletePerson}/>
    </div>
  )
}

export default App
